$light_bg_1: #eaeaea;
$light_bg_2: #d5d5d5;
$dark_bg_1: #111;
$dark_bg_2: #1e1e1e;
$dark_bg_3: #292929;
$dark_body_bg: #13202b;
$light_body_bg: #e0e0e0;
$sans: "source-sans", sans-serif;
$demi-sans: "demi-sans", sans-serif;
$mono: "mono", monospace;
$sans-light: "source-sans-light", sans-serif;
